<template>
  <header class="fixed top:0 left:0 w:full pt:25 pb:10" :class="{ 'menu-open': isToggle }">
    <div class="l-wp | max-w:1440">
      <div class="menu | flex jc:space-between ai:center">
        <router-link class="logo | flex-basis:120" to="/"><img src="./img/logo.svg" alt=""></router-link>

        <div class="toggle | hide@pc {fixed;top:20;right:30;w:40;h:40}@<pc" :class="{ 'is-active': isToggle }"
          @click="menuToggle">
          <div class="toggle__bar"></div>
          <div class="toggle__bar"></div>
          <div class="toggle__bar"></div>
        </div>
        <!-- PC menu -->
        <div class="menu-content | hide@<pc" :class="{ 'is-show': isToggle }">
          <ul class=" list | el-reset flex gap-x:20">
            <li class="list__item | flex ai:center jc:center" v-for="(val, index) in menu" :key="index">
              <a href="javascript:void(0);" class="list__link | block rel" @click="toBlock(val.anchor)"><span
                  class="block text:center" v-html="val.name"></span></a>
              <!-- <router-link class="list_link" :to="val.url"><span v-html="val.name"></span></router-link> -->
            </li>
          </ul>
        </div>
      </div>
    </div>
  </header>
  <!-- mobile Menu -->
  <div
    class="menu-content | hide@pc {fixed;top:0;left:0;w:300;h:full;py:50;bg:#202020;trs;translateX(-100%);z:999}@<pc {translateX(0)}.is-show"
    :class="{ 'is-show': isToggle }">
    <ul class=" list | el-reset flex gap-x:20@pc {gap-y:12;flex:col}@<pc">
      <li class="list__item | flex ai:center jc:center" v-for="(val, index) in menu" :key="index">
        <a href="javascript:void(0);" class="list__link | block rel" @click="toBlock(val.anchor)"><span
            class="block text:center" v-html="val.name"></span></a>
        <!-- <router-link class="list_link" :to="val.url"><span v-html="val.name"></span></router-link> -->
      </li>
    </ul>
  </div>
  <div
    class="hd-mask | hide@pc {fixed;top:0;left:0;bg:rgba(0,0,0,.3);w:full;h:full;z:1;bd:blur(0px);opacity:0;trs;pointer-events:none} {opacity:1;bd:blur(5px);pointer-events:auto}.is-show"
    :class="{ 'is-show': isToggle }" @click="menuToggle"></div>
</template>

<script setup>
import { defineOptions, ref } from 'vue'
import { getSetting } from '@api/website/getSetting'

defineOptions({ name: 'WebsiteHeader' })

// init
const isToggle = ref(false)
const menu = ref(getSetting().menu)

const menuToggle = () => { isToggle.value = !isToggle.value }

// 錨點
const toBlock = (i) => {
  // 獲取特定區塊位置
  const targetPosition = document.querySelector(i).offsetTop
  const headerHeight = document.querySelector('header').clientHeight

  const scrollTag = targetPosition - headerHeight

  window.scrollTo(0, scrollTag)

  menuToggle()
}

</script>

<style lang="sass" scoped>
@import ./style
</style>
