import { ref } from 'vue'
import { defineStore } from 'pinia'

export const useCounterStore = defineStore('counter', () => {
  const count = ref(100)

  const test =ref({
    test: 4564,
    test2: [0, 1, 2]
  })

  function increment() {
    count.value++
  }

  return {
    count,
    test,
    increment
  }
})