// useScroll.js

export const setupScroll = () => {
  let documentHeight = 0
  let windowHeight = 0
  let footerHeight = 0
  let threshold = 0

  // 計算高度和閾值
  const calculateDimensions = () => {
    documentHeight = document.documentElement.scrollHeight
    windowHeight = window.innerHeight
    const footerElement = document.querySelector('footer')
    footerHeight = footerElement ? footerElement.offsetHeight : 0
    threshold = documentHeight - windowHeight - footerHeight
  }

  const onScroll = () => {
    const scrollPosition = window.scrollY || window.pageYOffset
    const gotopElement = document.querySelector('.c-btn\\:gotop')
    if (!gotopElement) {
      console.error('回頂部按鈕未找到，請檢查 class 是否正確')
      return
    }

    if (scrollPosition > 0) {
      gotopElement.classList.add('is-show') // 顯示按鈕
      if (scrollPosition >= threshold) {
        gotopElement.classList.remove('is-move')
      } else {
        gotopElement.classList.add('is-move')
      }
    } else {
      gotopElement.classList.remove('is-show') // 隱藏按鈕
    }
  }

  const initScroll = () => {
    calculateDimensions()
    window.addEventListener('resize', calculateDimensions)
    window.addEventListener('scroll', onScroll)
  }

  return { initScroll }
}
